/* eslint-disable no-console */
<template>
  <div id="app">
    <div class="row no-gutters">
  <!--  -->
  <div class="toolbox col-sm-3 p-2 bg-white">
    <div class="form-group d-flex">
      <label for="city" class="col-form-label mr-2 text-right"></label>
      <div class="flex-fill">
        <!-- v-model -->
        <select id="city" class="form-control" v-model="select.city">
        <!--  -->
        <!-- option.value="{{ cityName.districts.name }}" -->
          <option v-bind:value="city.name" v-bind:key="city.name" v-for="city in cityName">
            <!-- v-for -->
            {{city.name}}
          </option>
        </select>
      </div>
    </div>
    <div class="form-group d-flex">
      <label for="dist" class="col-form-label mr-2 text-right"></label>
      <div class="flex-fill">
        <select id="dist" class="form-control" v-model="select.dist">
        <!--  -->
        <!-- v-bind:: -->
        <option :value="dist.name" :key="dist.name"
        v-for="dist in cityName.find((city) => city.name === select.city).districts">
          {{dist.name}}
        </option>
        </select>
      </div>
    </div>
  </div>

  <!--  UBike  -->
  <div class="col-sm-9">
    <div id="map"></div>
  </div>
</div>
  </div>
</template>

<script>
import L from 'leaflet'; // OSMgoogle map
import cityName from './assets/cityName.json';
export default {
  name: 'App',
  data: () => ({
    cityName,
    select: {
      city: '',
      dist: '',
    },
    ubikes: [],
    OSMap: [],
  }),
  computed: { // ubikesmarkerLayer
    youbikes() {
      return this.ubikes.filter((bike) => bike.sarea === this.select.dist);
    },
  },
  watch: { // youbikesaddMarkers()
    youbikes() {
      this.updateMap();
    },
  },
  methods: {
    updateMap() {
      // remove markers
      this.OSMap.eachLayer((layer) => {
        // 
        if (layer instanceof L.Marker) {
          this.OSMap.removeLayer(layer);
        }
      });
      // add markers
      this.youbikes.forEach((bike) => {
        L.marker([bike.lat, bike.lng])
          .bindPopup(
            `<p><strong style="font-size: 20px;">${bike.sna}</strong></p>
            <strong style="font-size: 16px; color: #d45345;">${bike.sbi} </strong><br>
            : ${bike.bemp}<br>
            <small>: ${bike.mday}</small>`,
          )
          .addTo(this.OSMap); // 
      });
      // move to new center
      this.cityName[0].districts.find((dist) => {
        if (dist.name === this.select.dist) {
          // this.OSMap.panTo(new L.LatLng(dist.latitude, dist.longitude)); // 
          this.OSMap.flyTo(new L.LatLng(dist.latitude, dist.longitude, 14)); // 
        }
        return dist.name === this.select.dist;
      });
    },
  },
  created() {
    const url = 'https://tcgbusfs.blob.core.windows.net/blobyoubike/YouBikeTP.json';
    axios.get(url).then((response) => {
      console.log(response.data);
      this.ubikes = Object.keys(response.data.retVal).map((key) => response.data.retVal[key]);
    });
  },
  mounted() {
    // initalize
    this.OSMap = L.map('map', {
      center: [25.041956, 121.508791],
      zoom: 18,
    });
    // add tile to map
    L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
      attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
      maxZoom: 18,
    }).addTo(this.OSMap);
    this.setView();
  },
};
</script>

<style lang="scss" scoped>
// @import 'bootstrap/scss/bootstrap';
#map {
  height: 100vh;
  position: relative;
}
</style>